<template>
  <div>
    <ul>
      <!--使用模板语法-->
      <li v-for="(item,index) in lists" :key="index" >
       标题为：{{ item.title }}
        <br>
        内容为：{{ item.content }}
      </li>
    </ul>
  </div>
</template>

<script>
  import store from '@/store'
    export default {
        name: "List",
      store,
      //这样就引入store的数据了
      //因为是动态列表，所以我们要动态的去监听
      computed:{
         lists(){
           return store.state.list
         }
      }
    }
</script>

